/*
  REM @mixin

  Usage:
  @include rem(property, value);
*/
html, body {
  height: 100%;
}

body {
  margin: 0;
  background-color: #fff;
  color: #0a0a0a;
}
body .inside {
  width: 90%;
  max-width: 1363px;
  margin-left: auto;
  margin-right: auto;
}

img {
  width: auto;
  height: auto;
  max-width: 100%;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 12px;
  font-size: 1.2rem;
  line-height: 1.4em;
  font-family: "Droid Sans", sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #0a0a0a;
}
@media screen and (min-width: 768px) {
  body {
    font-size: 17px;
    font-size: 1.7rem;
  }
}
@media screen and (min-width: 1440px) {
  body {
    font-size: 20px;
    font-size: 2rem;
  }
}

p, ul, ol, img {
  margin: 0 0 1em 0;
}

b, strong {
  font-family: "Droid Sans", sans-serif;
  font-weight: 700;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Droid Sans", sans-serif;
  font-weight: 700;
}

ul, ol {
  padding-left: 1em;
}